.scene {
  min-width: 11rem;
  height: 35rem;
  perspective: 600px;
  margin: 1rem;
  cursor: pointer;
}
.card {
  width: 100%;
  height: 100%;
  position: relative;
  transition: transform 1s;
  transform-style: preserve-3d;
}
.face {
  position: absolute;
  height: 100%;
  width: 100%;
  backface-visibility: hidden;
}
.face.back {
  transform: rotateY(180deg);
}
.card.is-flipped {
  transform: rotateY(180deg);
}
